#todo-list {
    list-style-type: none;
    padding: 0;
    margin: 20px auto;
    width: 600px;
  }
  
  #todo-list li {
    padding: 15px;
    background-color: #fff;
    border-radius: 8px;
    margin-bottom: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  button {
    background-color: #ff4d4f;
    color: #fff;
    border: none;
    padding: 8px 12px;
    border-radius: 5px;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #f5222d;
  }
  
  .completed {
    text-decoration: line-through;
    color: #999;
  }
  